/* eslint-disable */
let scraper2 = `using System;
using System.IO;
using System.Linq;
using System.Net.Http;
using System.Text.RegularExpressions;
using System.Threading.Tasks;
using Newtonsoft.Json;
using ExtensionClass;
using ImageGrabberConsoleApp.Maps;

namespace ImageGrabberConsoleApp.Grabbers
{
    class Scraper2
    {
        //note: pagination specific to scraper2
        #region Scraper
        // ({.*}) everything between {}              (\d+) all numbers
        private static string html = string.Empty, nextPageHref = string.Empty;
        private static int countTotal = 0, countRelative = 0, picturesSkipped = 0;
        private static readonly HtmlAgilityPack.HtmlDocument doc = new HtmlAgilityPack.HtmlDocument();
        private static readonly Regex filterPagination = new Regex(@"(\d+)"), filterFragmentedJson = new Regex(@"{.*}");

        public void Scrape(string url, HttpClient client, int numOfPages)   //numOfPages, the number of pages the user wants to grab from
        {
            try
            {
                html = client.GetStringAsync(url).GetAwaiter().GetResult();
                doc.LoadHtml(html);
            }
            catch (Exception)
            {
                Console.WriteLine("Something went wrong with the inputted url");
                return;
            }
            string newUrl = string.Empty;
            //grab text from the first title tag
            string title = doc.DocumentNode.Descendants("title").First().InnerText;

            char[] seperators = new char[] { '|', '\\', '/' };

            if (seperators.Any(title.Contains))
                title = title.Replace(seperators, " ");

            //create folder on desktop with title
            string root = Environment.GetFolderPath(Environment.SpecialFolder.Desktop); //finds the desktop of current user and stores in string root
            Directory.CreateDirectory(root + $@"\{title}");               //creates necessary folders/directories
            string downloadFolderPath = root + $@"\{title}\";

            /*
            convert matches to string array then string array to int array
            range paginationRange[1] -> paginationRange[paginationRange.Length - 2]
            */

            int startPage = 1;
            int endPage = 1;
            if (doc.DocumentNode.Descendants("div").Where(x => x.HasClass("pagination")).Any())
            {
                var paginationRange = filterPagination.Matches(doc.DocumentNode.Descendants("div").Where(x => x.HasClass("pagination")).First().InnerText).Cast<Match>().Select(m => m.Value).ToArray().Select(int.Parse).ToArray();
                startPage = int.Parse(doc.DocumentNode.Descendants("em").Where(x => x.HasClass("current")).First().InnerText);  //em tag with class current, gives value indicating current page
                endPage = paginationRange[paginationRange.Length - 2];
            }

            //if no input was made for numOfPages default to max pages grabbed, else endPage is set as desired
            if ((numOfPages > 0) && ((startPage + numOfPages - 1) <= endPage))
                endPage = startPage + numOfPages - 1;

            //clear url of queries to be able to attach next_page href
            if (url.Contains("Scraper2 url"))
                newUrl = url.Substring(0, 20);
            else
                newUrl = url.Substring(0, 16);

            if (doc.DocumentNode.Descendants("a").Where(x => x.HasClass("next_page")).Any())
                nextPageHref = doc.DocumentNode.Descendants("a").Where(x => x.HasClass("next_page")).First().Attributes["href"].Value;

            int pageCounter = 0;
            countRelative = 0;
            picturesSkipped = 0;
            //loop through the pagination range and gets the html of each
            for (int i = startPage; i <= endPage; i++)
            {
                if (i > startPage) //gets the html of next page after the initial url has been processed
                {
                    try
                    {
                        html = client.GetStringAsync(newUrl + nextPageHref).GetAwaiter().GetResult();
                        doc.LoadHtml(html);

                        if (doc.DocumentNode.Descendants("a").Where(x => x.HasClass("next_page")).Any())
                            nextPageHref = doc.DocumentNode.Descendants("a").Where(x => x.HasClass("next_page")).First().Attributes["href"].Value;
                    }
                    catch (Exception)
                    {
                        Console.WriteLine($"Something went wrong with the url: ( {newUrl}{nextPageHref} )");
                        return;
                    }
                }
                if (!doc.DocumentNode.Descendants("script").Where(x => x.InnerText.Contains("Post.register")).Any())
                    continue;


                //extract multiple jsonblock from html and skips first block (first block has unneccessary info), creates array then joins all idexes together into string json
                var jsonArray = filterFragmentedJson.Matches(doc.DocumentNode.Descendants("script").Where(x => x.InnerText.Contains("Post.register")).First().InnerText).Cast<Match>().Select(m => m.Value).Skip(1).ToArray();
                string json = ("{\"itemListElement\":[" + string.Join(",", jsonArray) + "]}");
                var data = JsonConvert.DeserializeObject<Scraper2Map>(json);    //map json to Scraper2Map

                if (!data.ItemListElements.Any())
                    continue;
                //loopthrough data.ItemListElements to download pictures for that specific pagination
                for (int j = 0; j < data.ItemListElements.Length; j++)
                {
                    Console.Title = $"Downloading picture {j + 1}/{data.ItemListElements.Length} of page {i}/{endPage}! Downloaded from Url: {countRelative} Total Downloaded: {countTotal} Skipped: {picturesSkipped}";
                    //parse the picture url to extract suitable file name
                    string fileName = data.ItemListElements[j].id + ".jpg";
                    //check if picture already exists, continue
                    if (File.Exists(downloadFolderPath + fileName))
                    {
                        picturesSkipped++;
                        Console.WriteLine($"Skipped {data.ItemListElements[j].jpeg_url}");
                        continue;
                    }

                    //download picture with GetMedia
                    GetMedia(data.ItemListElements[j].jpeg_url, downloadFolderPath + fileName, client).GetAwaiter().GetResult();
                    countTotal++;
                    countRelative++;
                }
            }
            Console.WriteLine($"[Pages Downloaded: {pageCounter}] [Total Media Downloaded: {countTotal}] [Skipped: {picturesSkipped}]");
            Console.Title = "Finished Grabbing, waiting for new URL!";
        }

        private static async Task GetMedia(string mediaUrl, string downloadPath, HttpClient client) //downloads file to specified path using httpclient
        {
            File.WriteAllBytes(downloadPath, (await client.GetByteArrayAsync(mediaUrl)));
        }

        #endregion
    }
}
`;

export default scraper2;