let setMap = `using Newtonsoft.Json;

namespace SkillBuilderMHRise.JsonMaps
{
    class SetMap
    {
        [JsonProperty("Sets")]
        public Sets[] sets { get; set; }

        public class Sets
        {
            public string SetName { get; set; }

            [JsonProperty("Pieces")]
            public Pieces pieces { get; set; }
        }

        public class Pieces
        {
            [JsonProperty("Helm")]
            public Helm helm { get; set; }

            [JsonProperty("Chest")]
            public Chest chest { get; set; }

            [JsonProperty("Hands")]
            public Hands hands { get; set; }

            [JsonProperty("Waist")]
            public Waist waist { get; set; }

            [JsonProperty("Legs")]
            public Legs legs { get; set; }

            
        }

        public abstract class Part
        {
            public string Name { get; set; }
            public string Id { get; set; }

            [JsonProperty("Skills")]
            public Skills[] skills { get; set; }

            [JsonProperty("Slots")]
            public Slots slots { get; set; }
        }

        public class Helm : Part { }

        public class Chest : Part { }

        public class Hands : Part { }

        public class Waist : Part { }

        public class Legs : Part { }

        public class Skills
        {
            public string Name { get; set; }
            public string Level { get; set; }
        }

        public class Slots
        {
            public string Slot1 { get; set; }
            public string Slot2 { get; set; }
            public string Slot3 { get; set; }
        }
    }
}
`;

export default setMap;