/* eslint-disable */
let scraper3 = `using System;
using System.Collections.Generic;
using System.IO;
using System.Linq;
using System.Net.Http;
using System.Text.RegularExpressions;
using System.Threading.Tasks;

namespace ImageGrabberConsoleApp.Grabbers
{
    class Scraper3
    {
        //note: pagination specific to Scraper3
        #region Scraper
        // ([^\/]+$) everything after last /              (\d+) all numbers
        private static string html = string.Empty, nextPageHref = string.Empty;
        private static int countTotal = 0, countRelative = 0, picturesSkipped = 0;
        private static readonly HtmlAgilityPack.HtmlDocument doc = new HtmlAgilityPack.HtmlDocument();
        private static readonly Regex filterName = new Regex(@"([^\/]+$)");
        private static bool downloadAll = false;

        public void Scrape(string url, HttpClient client, int numOfPages) //numOfPages, the number of pages the user wants to grab from
        {
            try
            {
                html = client.GetStringAsync(url).GetAwaiter().GetResult();
                doc.LoadHtml(html);
            }
            catch (Exception)
            {
                Console.WriteLine("Something went wrong with the inputted url");
                return;
            }
            string newUrl = string.Empty;

            string title = doc.DocumentNode.Descendants("title").First().InnerText;

            if (title.Contains('|'))
                title = title.Replace('|', ' ');

            //create folder on desktop with title
            string root = Environment.GetFolderPath(Environment.SpecialFolder.Desktop); //finds the desktop of current user and stores in string root
            Directory.CreateDirectory(root + $@"\{title}");               //creates necessary folders/directories
            string downloadFolderPath = root + $@"\{title}\";

            //convert matches to string array then string array to int array
            int startPage = 1;
            //if (doc.DocumentNode.Descendants("li").Where(x => x.HasClass("current-page")).Any())
                //startPage = int.Parse(doc.DocumentNode.Descendants("li").Where(x => x.HasClass("current-page")).First().InnerText);  //li tag with class current-page, gives value indicating current page

            //gets rid of pagination querry that is included in original url
            newUrl = url.Substring(0, 26);
            var endPage = 0;
            if (numOfPages > 0) //setup endPage to break out of the while loop if a desired number of pages was inputted
                endPage = startPage + numOfPages - 1;
            if (endPage == 0)
                downloadAll = true;
            countRelative = 0;
            picturesSkipped = 0;
            //loop through the pagination range and gets the html of each
            int i = startPage;
            int pageCounter = 0;
            bool continueLoop = true;
            while (continueLoop)    //while loop because some urls do not include information on a last page
            {
                if (i > startPage) //gets the html of next page after the initial url has been processed
                {
                    try
                    {
                        html = client.GetStringAsync(newUrl + nextPageHref).GetAwaiter().GetResult();
                        doc.LoadHtml(html);
                    }
                    catch (Exception)
                    {
                        Console.WriteLine($"Something went wrong with the url: ( {newUrl}{nextPageHref} )");
                        return;
                    }
                }
                //last page has a paginator-next class in a "span" Descendant with no href
                if (doc.DocumentNode.Descendants("a").Where(x => x.HasClass("paginator-next")).Any())   //checks for the tag containing information on the next page and gets the href if it exists, if not, set the loop to break after this run through
                    nextPageHref = doc.DocumentNode.Descendants("a").Where(x => x.HasClass("paginator-next")).First().Attributes["href"].Value;
                else
                    continueLoop = false;

                if (!doc.DocumentNode.Descendants("article").Where(x => x.HasClass("post-preview")).Any())  //if for some reason the page does not have media to download, move on to next loop iteration
                {
                    i++;
                    continue;
                }
                    
                //extract the media url from the article tag which has the class post-preview and add it to a list, use regex on the url to create a filename
                List<string> fileUrl = new List<string>();
                List<string> fileName = new List<string>();
                foreach (var node in doc.DocumentNode.Descendants("article").Where(x => x.HasClass("post-preview")))
                {
                    fileUrl.Add(node.Attributes["data-file-url"].Value);
                    fileName.Add(filterName.Match(node.Attributes["data-file-url"].Value).Value);
                }

                if (!fileUrl.Any())     //if there was an article tag but no media urls, continue to the next iteration
                {
                    i++;
                    continue;
                }
                    
                //loopthrough fileUrl to download pictures for that specific pagination
                for (int j = 0; j < fileUrl.Count; j++)
                {
                    Console.Title = $"Downloading picture {j + 1}/{fileUrl.Count} of page {i}{(numOfPages > 0 ? "/" + endPage + "!" : "...")} Downloaded from Url: {countRelative} Total Downloaded: {countTotal} Skipped: {picturesSkipped}";
                    //check if picture already exists, continue
                    if (File.Exists(downloadFolderPath + fileName[j]))
                    {
                        picturesSkipped++;
                        Console.WriteLine($"Skipped {fileUrl[j]}");
                        continue;
                    }

                    //download picture with GetMedia
                    GetMedia(fileUrl[j], downloadFolderPath + fileName[j], client).GetAwaiter().GetResult();
                    countTotal++;
                    countRelative++;
                }
                i++;
                pageCounter++;
                if (numOfPages > 0)
                    if (i > endPage && !downloadAll)
                        break;
            }
            Console.WriteLine($"[Pages Downloaded: {pageCounter}] [Total Media Downloaded: {countTotal}] [Skipped: {picturesSkipped}]");
            Console.Title = "Finished Grabbing, waiting for new URL!";
        }

        private static async Task GetMedia(string mediaUrl, string downloadPath, HttpClient client) //downloads file to specified path using httpclient
        {
            File.WriteAllBytes(downloadPath, (await client.GetByteArrayAsync(mediaUrl)));
        }

        #endregion
    }
}

`;

export default scraper3;