let InstagramProfileMap = `using Newtonsoft.Json;


namespace ConsoleApp3
{
    class InstagramProfile
    {
        [JsonProperty("entry_data")]
        public EntryData entryData { get;set; }
        
        public class EntryData
        {
            [JsonProperty("ProfilePage")]
            public ProfilePage[] profilePage { get; set; }
        }

        public class ProfilePage
        {
            [JsonProperty("graphql")]
            public Graphql graphql { get; set; }
        }

        public class Graphql
        {
            [JsonProperty("user")]
            public User user { get; set; }
        }

        public class User
        {
            public string username { get; set; }

            [JsonProperty("edge_owner_to_timeline_media")]
            public PostEdges postEdges { get; set; }
        }

        public class PostEdges
        {
            [JsonProperty("edges")]
            public Edges[] edges { get; set; }
        }
        public class Edges
        {
            [JsonProperty("node")]
            public Node node { get; set; }
        }

        public class Node
        {
            public string shortcode { get; set; }
            public string taken_at_timestamp { get; set; }

            [JsonProperty("edge_media_to_caption")]
            public CaptionEdge captionEdge { get; set; }
        }

        public class CaptionEdge
        {
            [JsonProperty("edges")]
            public Edges2[] edges2 { get; set; }
        }

        public class Edges2
        {
            [JsonProperty("node")]
            public Node2 node2 { get; set; }
        }

        public class Node2
        {
            public string text { get; set; }
        }

    }
}
`;

export default InstagramProfileMap;