/* eslint-disable */
let scraper1 = `using System;
using System.Collections.Generic;
using System.IO;
using System.Linq;
using System.Net.Http;
using System.Text.RegularExpressions;
using System.Threading.Tasks;
using ImageGrabberConsoleApp.Maps;
using Newtonsoft.Json;

namespace ImageGrabberConsoleApp.Grabbers
{
    class Scraper1
    {
        //note: pagination specific to scraper1 url
        #region Scraper
        // ([^\/]+$) everything after last /              (\d+) all numbers
        private static string html = string.Empty;
        private static int countTotal = 0, countRelative = 0, picturesSkipped = 0;
        private static readonly HtmlAgilityPack.HtmlDocument doc = new HtmlAgilityPack.HtmlDocument();
        private static readonly Regex filterPagination = new Regex(@"(\d+)"), filterName = new Regex(@"([^\/]+$)");

        public void Scrape(string url, HttpClient client, int numOfPages) //numOfPages, the number of pages the user wants to grab from
        {
            try
            {
                SignIn(client);
                html = client.GetStringAsync(url).GetAwaiter().GetResult();
                doc.LoadHtml(html);
            }
            catch (Exception)
            {
                Console.WriteLine("Something went wrong with the inputted url");
                return;
            }
            string newUrl = string.Empty;

            string title = doc.DocumentNode.Descendants("title").First().InnerText;

            //remove the character '|' so the title can be used as the folder name
            if (title.Contains('|'))
                title = title.Replace('|', ' ');

            //create folder on desktop with title
            string root = Environment.GetFolderPath(Environment.SpecialFolder.Desktop); //finds the desktop of current user and stores in string root
            Directory.CreateDirectory(root + $@"\{title}");               //creates necessary folders/directories
            string downloadFolderPath = root + $@"\{title}\";

            //convert matches to string array then string array to int array
            var paginationRange = filterPagination.Matches(doc.DocumentNode.Descendants("p").Where(x => x.HasClass("pagination")).First().InnerText).Cast<Match>().Select(m => m.Value).ToArray().Select(int.Parse).ToArray();
            var endPage = paginationRange[1];
            //if no imput was made for numOfPages default to max pages grabbed, else endPage is set as desired
            if ((numOfPages > 0) && ((paginationRange[0] + numOfPages - 1) <= paginationRange[1]))
                endPage = paginationRange[0] + numOfPages - 1;
            
            //gets rid of pagination querry if it was included in original url
            if (url.Contains($"&p={paginationRange[0]}"))
                newUrl = url.Substring(0, url.Length - 4);
            else
                newUrl = url;

            int pageCounter = 0;
            countRelative = 0;
            picturesSkipped = 0;
            //loop through the pagination range and gets the html of each
            for (int i = paginationRange[0]; i <= endPage; i++)
            {
                if (i > paginationRange[0]) //gets the html of next page after the initial url has been processed
                {
                    try
                    {
                        html = client.GetStringAsync(newUrl + $"&p={i}").GetAwaiter().GetResult();
                        doc.LoadHtml(html);
                    }
                    catch (Exception)
                    {
                        Console.WriteLine($"Something went wrong with the url: ( {newUrl}&p={i} )");
                        return;
                    }
                }
                //if there is no next page and if there is no itemlist
                if (!doc.DocumentNode.Descendants("p").Where(x => x.HasClass("pagination")).Any() && !doc.DocumentNode.Descendants("script").Where(x => x.InnerText.Contains("itemListElement")).Any())
                    continue;
                //extract jsonblock from html
                string json = doc.DocumentNode.Descendants("script").Where(x => x.InnerText.Contains("itemListElement")).First().InnerText;
                var data = JsonConvert.DeserializeObject<Scraper1Map>(json);    //map json to Scraper1Map

                //loopthrough data.ItemListElements to download pictures for that specific pagination
                for (int j = 0; j < data.ItemListElements.Length; j++)
                {
                    Console.Title = $"Downloading picture {j + 1}/{data.ItemListElements.Length} of page {i}/{endPage}! Downloaded from Url: {countRelative} Total Downloaded: {countTotal} Skipped: {picturesSkipped}";
                    //parse the picture url to extract suitable file name
                    string fileName = filterName.Match(data.ItemListElements[j].url).Value;
                    //check if picture already exists, continue
                    if (File.Exists(downloadFolderPath + fileName))
                    {
                        picturesSkipped++;
                        Console.WriteLine($"Skipped {data.ItemListElements[j].url}");
                        continue;
                    }

                    //download picture with GetMedia
                    GetMedia(data.ItemListElements[j].url, downloadFolderPath + fileName, client).GetAwaiter().GetResult();
                    countTotal++;
                    countRelative++;
                }
            }
            Console.WriteLine($"[Pages Downloaded: {pageCounter}] [Total Media Downloaded: {countTotal}] [Skipped: {picturesSkipped}]");
            Console.Title = "Finished Grabbing, waiting for new URL!";
        }

        private static void SignIn(HttpClient client)
        {
            //string ref = "/" + filterName.Match(redirect).Value;
            string url = "scraper1 login url";
            FormUrlEncodedContent form = new FormUrlEncodedContent(new Dictionary<string, string>
            {
                ["ref"] = "",
                ["name"] = "",
                ["password"] = "",
                ["login"] = "",
            });

            HttpResponseMessage response = client.PostAsync(url, form).GetAwaiter().GetResult();

            if (response.StatusCode != System.Net.HttpStatusCode.RedirectMethod)
                Console.WriteLine("Login failed!");
        }

        private static async Task GetMedia(string mediaUrl, string downloadPath, HttpClient client) //downloads file to specified path using httpclient
        {
            File.WriteAllBytes(downloadPath, (await client.GetByteArrayAsync(mediaUrl)));
        }

        #endregion
    }
}
`;

export default scraper1;