let Program = `using Newtonsoft.Json;
using System;
using System.Collections.Generic;
using System.IO;
using System.Linq;
using System.Net.Http;
using System.Text;
using System.Threading.Tasks;
using HtmlAgilityPack;
using ConsoleApp3;
using InstaGrabber;
using static instagramPagesTest.InstagramNextPage;

namespace instagramPagesTest
{
    class Program
    {
        private static HttpClient client = new HttpClient();
        private static HtmlAgilityPack.HtmlDocument doc = new HtmlAgilityPack.HtmlDocument(), doc2 = new HtmlAgilityPack.HtmlDocument();
        private static string json, html, instagramUploadDate, shortcode, end_cursor, prev_end_cursor = string.Empty, html2, url,
                              root = Environment.GetFolderPath(Environment.SpecialFolder.Desktop), instagramProfileAt, profile = string.Empty,
                              username = string.Empty, LogPath = string.Empty, caption = string.Empty;
        private static string[] mediaURLs;
        private static List<string> shortCodeUrls = new List<string>();
        private static readonly DateTime epoch = new DateTime(1970, 1, 1, 0, 0, 0, DateTimeKind.Utc);
        private static DateTime eToUtc;
        private static StringBuilder sb = new StringBuilder();
        private static List<string> input = new List<string>();
        private static int counter = 1, totalCounter, postCount;
        private static bool nullUser = false, doesNotExist = false, getCaptions = false, getMedia = false;

        static void Main(string[] args)
        {
            Console.SetBufferSize(250, 1000);
            Console.ForegroundColor = ConsoleColor.DarkCyan;
            Console.Title = "Profile Grabber!";

            client.DefaultRequestHeaders.Add("User-Agent", "user agent info");

            Directory.CreateDirectory(root + @"\InstaGrabber\Downloaded Files\New Insta");
            while (true)
            {
                nullUser = false;
                end_cursor = string.Empty;  //pagination value used to access the next page
                counter = 1; //keeps track of pagination of instagram posts

                while (true)
                {
                    try
                    {
                        Input();

                        if (profile.All(char.IsDigit))
                        {    //if an instagram profile's id is given instead of username, get the username
                            username = GetUsername(profile);
                            postCount = Convert.ToInt32(GetPostCount(username));
                        }
                        else if (profile == "n")    //if exitting program
                            break;
                        else
                        {  //if an instagram profile's username is given, get the id
                            username = profile;
                            GetId(profile);
                        }
                        if (!doesNotExist)
                        {    //create directories if they do not already exist
                            Directory.CreateDirectory(root + $"\\InstaGrabber\\Downloaded Files\\New Insta\\@{username}");
                            if (getCaptions)
                            {
                                LogPath = root + $"\\InstaGrabber\\Downloaded Files\\New Insta\\@{username}\\@{username}_Captions.txt";
                                if (!File.Exists(root + $"\\InstaGrabber\\Downloaded Files\\New Insta\\@{username}\\@{username}_Captions.txt"))
                                    File.Create(root + $"\\InstaGrabber\\Downloaded Files\\New Insta\\@{username}\\@{username}_Captions.txt").Close();
                            }
                            break;
                        }

                        doesNotExist = false;
                    }
                    catch (Exception e)
                    {
                        Console.WriteLine(e);
                    }

                }
                if (profile == "n")
                    break;
                totalCounter = 0;
                Console.Clear();
                Console.Title = $"Attempting to grab {postCount} post(s) from {((postCount % 50 > 0) ? postCount / 50 + 1 : postCount / 50)} page(s)! [Username:{username}] [ID:{profile}]!";
                Console.WriteLine(!string.IsNullOrEmpty(username) ? $"Attempting to grab {postCount} post(s) from {((postCount % 50 > 0) ? postCount / 50 + 1 : postCount / 50)} page(s)! [Username:{username}] [ID:{profile}]!\n" +
                    $"Files saved to directory \\Desktop\\InstaGrabber\\Downloaded Files\\New Insta\\@{username}" : $"Error from {profile}!");

                while (true)
                {  //grab urls of all post of the current page
                    if (!getCaptions && !getMedia)
                        break;
                    prev_end_cursor = end_cursor;   //sets the pagination cursor
                    url = $"api query url";
                    GrabShortCodes(url);

                    if (nullUser)
                        break;

                    Console.WriteLine($"Grabbing! [Page:{counter} of {((postCount % 50 > 0) ? postCount / 50 + 1 : postCount / 50)}] [Profile:{username}] [ID:{profile}] Next:( {end_cursor} )");
                    foreach (string shortCode in shortCodeUrls) //grab media for every post url
                        InstagramGrabAsync(shortCode).GetAwaiter().GetResult();

                    if (prev_end_cursor == end_cursor)
                        break;

                    counter++;
                }
                if (!nullUser)
                {
                    Console.WriteLine($"Finished Grabbing From @{username}! Downloaded {totalCounter} of {postCount} post(s)!\n");
                    Console.Title = $"Finished! [@{username}] [{profile}] {(getCaptions ? "[Downloaded All Posts and Captions!]" : "[Downloaded All Posts!]")} [Downloaded:{totalCounter} post(s)!]";
                }
            }
        }

        private static void Input() //process the user input
        {
            while (true)
            {
                Console.WriteLine($"Enter the ID of the profile to grab or type (n) to exit.{Environment.NewLine}Type \"profileName/profileID\" \"captions/media/(leave emtpy to download both)\"");
                input = Console.ReadLine().Split(' ').ToList();
                if (input.All(x => string.IsNullOrEmpty(x.Trim())))
                    Console.WriteLine("Nothing was entered!");
                else
                {
                    if (input.Count == 2)
                    { //if either captions or media needs to be downloaded
                        profile = input[0];
                        if (input[1] == "captions")
                        {
                            getCaptions = true;
                            getMedia = false;
                        }
                        else if (input[1] == "media")
                        {
                            getCaptions = false;
                            getMedia = true;
                        }
                        else
                        {
                            Console.WriteLine($"{input[1]} is not a valid input!");
                            continue;
                        }
                        break;
                    }
                    else if (input.Count > 2)
                        Console.WriteLine($"Accepts only one or two strings.");
                    else
                    {  //if only the instagram profile was input
                        profile = input[0];
                        getCaptions = true;
                        getMedia = true;
                        break;
                    }
                }
            }
        }

        private static void DoesNotExist()  //console message used during error
        {
            Console.ForegroundColor = ConsoleColor.DarkRed;
            Console.WriteLine($"Failed To grab from {profile}! Make sure the account exists!");
            Console.ForegroundColor = ConsoleColor.DarkCyan;

        }

        private static string GetUsername(string profile)   //gets the string username of a profile when the id is provided
        {
            string url = $"api user url";
            try
            {
                string tempJson = client.GetStringAsync(url).GetAwaiter().GetResult();
                var data = JsonConvert.DeserializeObject<InstagramProfile>(tempJson);
                return data.user2.username;
            }
            catch (Exception)
            {
                DoesNotExist();
                doesNotExist = true;
                return string.Empty;
            }
        }

        private static void GetId(string profileName)   //gets the id of a profile if the username is provided
        {
            string temp = $"https://www.instagram.com/{profileName}/";
            try
            {
                html = client.GetStringAsync(temp).GetAwaiter().GetResult();
            }
            catch (Exception)
            {
                DoesNotExist();
                doesNotExist = true;
                return;
            }
            doc.LoadHtml(html);
            json = doc.DocumentNode.Descendants().Where(x => x.InnerText.StartsWith("window._sharedData")).First().InnerText;   //extract the json block from the scraped html
            var data = JsonConvert.DeserializeObject<InstagramProfile>(json.Substring(21, json.Length - 22));   //map the json block so the data can be easily accessed
            postCount = Convert.ToInt32(data.entryData.profilePage[0].graphql.user.postEdges.count);
            if (data != null)
                profile = data.entryData.profilePage[0].graphql.user.id;
            else
                doesNotExist = true;
        }

        private static string GetPostCount(string profileName)  //gets the number of posts a user has
        {
            string temp = $"https://www.instagram.com/{profileName}/";
            try
            {
                html = client.GetStringAsync(temp).GetAwaiter().GetResult();
                doc.LoadHtml(html);
                json = doc.DocumentNode.Descendants().Where(x => x.InnerText.StartsWith("window._sharedData")).First().InnerText;
                var data = JsonConvert.DeserializeObject<InstagramProfile>(json.Substring(21, json.Length - 22));
                return data.entryData.profilePage[0].graphql.user.postEdges.count;
            }
            catch (Exception)
            {
                DoesNotExist();
                doesNotExist = true;
                return string.Empty;
            }
        }

        private static void GrabShortCodes(string url)  //grabs the urls to every post on the current pagination
        {
            shortCodeUrls.Clear();
            try
            {
                html2 = client.GetStringAsync(url).GetAwaiter().GetResult();
            }
            catch (Exception e)
            {
                Console.ForegroundColor = ConsoleColor.DarkRed;
                Console.WriteLine($"Error When Attempting To Grab ShortCodes.\n{e}");
                Console.ForegroundColor = ConsoleColor.DarkCyan;
                nullUser = true;
                return;
            }

            var data = JsonConvert.DeserializeObject<InstagramNextPage>(html2); //map the json block
            if (data.data.user == null)
            {
                nullUser = true;
                return;
            }
            var profileEdge = data.data.user.edgeToMedia.edges;

            if (!String.IsNullOrEmpty(data.data.user.edgeToMedia.pageInfo.end_cursor))
                end_cursor = data.data.user.edgeToMedia.pageInfo.end_cursor;

            foreach (Edges e in profileEdge)    //loop through the json data property containing post urls
                shortCodeUrls.Add("https://www.instagram.com/p/" + e.mediaNode.shortcode);
        }

        private static async Task InstagramGrabAsync(string url)    //downloads the captions and media of a post
        {
            bool downloaded = false;
            string date = string.Empty;    //string for concatenation of path and file name
            int downCount = 1;  //keeps track of how many posts are donwloaded
            string path = root + @"\InstaGrabber\Downloaded Files\New Insta";
            try
            {
                html = client.GetStringAsync(url).GetAwaiter().GetResult();
                GrabUrls(html);        //finds media urls from the post, save and trim to mediaURLs
                date = instagramUploadDate;
            }
            catch (Exception e)
            {
                Console.WriteLine(e);
                return;
            }

            for (int j = 0; j < mediaURLs.Length; j++)
            { //Downloads or opens file in browser 
                //prevents media from being overwritten, by checking if it already exits
                if (File.Exists(path + "\\" + instagramProfileAt + "\\" + date + "_" + shortcode + "_" + downCount + ".jpg") || File.Exists(path + "\\" + instagramProfileAt + "\\" + date + "_" + shortcode + "_" + downCount + ".mp4"))
                {
                    continue;
                }

                //if url in mediaURLs is jpg, download as jpg
                if (mediaURLs[j].Contains("jpg", StringComparison.OrdinalIgnoreCase))
                {
                    try
                    {
                        if (getMedia)
                            await GetMedia(mediaURLs[j], path + "\\" + instagramProfileAt + "\\" + date + "_" + shortcode + "_" + downCount + ".jpg", client);
                    }
                    catch (Exception)
                    {
                        Console.ForegroundColor = ConsoleColor.DarkMagenta;
                        Console.WriteLine($"{mediaURLs[j]} did not download {shortcode} https://www.instagram.com/p/{shortcode} {Environment.NewLine}");
                        Console.ForegroundColor = ConsoleColor.DarkCyan;
                        downCount++;
                        continue;
                    }

                    downCount++;    //increase download count
                }
                else if (mediaURLs[j].Contains("mp4", StringComparison.OrdinalIgnoreCase))
                {   //if url in mediaURLs is mp4, download as mp4
                    try
                    {
                        if (getMedia)
                            await GetMedia(mediaURLs[j], path + "\\" + instagramProfileAt + "\\" + date + "_" + shortcode + "_" + downCount + ".mp4", client);
                    }
                    catch (Exception)
                    {
                        Console.ForegroundColor = ConsoleColor.DarkMagenta;
                        Console.WriteLine($"{mediaURLs[j]} did not download {shortcode} https://www.instagram.com/p/{shortcode} {Environment.NewLine}");
                        Console.ForegroundColor = ConsoleColor.DarkCyan;
                        downCount++;
                        continue;
                    }
                    downCount++;    //increase download count
                }
                downloaded = true;
            }

            if (downloaded)
            {
                totalCounter++; //update current downloading status
                Console.Title = $"Grabbing! [@{username}] [{profile}] {(getCaptions ? "[Downloading Posts and Captions!]" : "[Downloading Posts!]")} " +
                    $"[Page:{counter} of {((postCount % 50 > 0) ? postCount / 50 + 1 : postCount / 50)}] [Downloaded:{totalCounter} of {postCount}]";
                if (getCaptions)
                {  //write captions to txt file
                    sb.Append($"[{eToUtc}] [ShortCode: {shortcode} ] [URL: https://www.instagram.com/p/{shortcode} ]{Environment.NewLine}[Comment:{caption}]{Environment.NewLine}{Environment.NewLine}");
                    File.AppendAllText(LogPath, sb.ToString());
                    sb.Clear();
                }
            }
        }

        private static void GrabUrls(string file) //uses regex to find jpg and mp4 urls and store them
        {
            doc.LoadHtml(file);
            json = doc.DocumentNode.Descendants().Where(x => x.InnerText.StartsWith("window._sharedData")).First().InnerText;   //extract json block
            var data = JsonConvert.DeserializeObject<InstagramPost>(json.Substring(21, json.Length - 22));  //map extracted json
            eToUtc = epoch.AddSeconds(Convert.ToDouble(data.entryData.postPage.First().graphql.media.taken_at_timestamp));
            var type = data.entryData.postPage.First().graphql.media.__typename;
            var instaMedia = data.entryData.postPage.First().graphql.media;
            var userName = data.entryData.postPage.First().graphql.media.owner.username;
            instagramUploadDate = eToUtc.Year + "-" + eToUtc.Month + "-" + eToUtc.Day;
            instagramProfileAt = "@" + userName;
            shortcode = data.entryData.postPage[0].graphql.media.shortcode;

            if (getCaptions)
            {
                caption = "No Caption!";
                if (data.entryData.postPage[0].graphql.media.caption.captionEdges.Any())    //checks if a caption was included with the post
                    caption = data.entryData.postPage[0].graphql.media.caption.captionEdges[0].captionNode.text;
            }

            if (type == "GraphSidecar")
            {   //property value indicating multiple media
                var edges = data.entryData.postPage[0].graphql.media.children.edges;
                int i = 0;
                string tempUrl = "";
                mediaURLs = new string[edges.Length];

                foreach (InstagramPost.Edges e in edges)
                {
                    if (e.node.__typename == "GraphImage")
                        tempUrl = e.node.resources.Last().src;
                    else if (e.node.__typename == "GraphVideo")
                        tempUrl = e.node.video_url;

                    mediaURLs[i] = tempUrl;
                    i++;
                }
            }

            if (type == "GraphImage")
            { //property value indicating a single image
                var singleImg = data.entryData.postPage.First().graphql.media.resources.Last().src;
                mediaURLs = new string[1];
                mediaURLs[0] = singleImg;
            }

            if (type == "GraphVideo")
            { //property value indicating a single video
                var singleVid = data.entryData.postPage.First().graphql.media.video_url;
                mediaURLs = new string[1];
                mediaURLs[0] = singleVid;
            }
        }

        private static async Task GetMedia(string mediaUrl, string downloadPath, HttpClient client)
        {
            File.WriteAllBytes(downloadPath, (await client.GetByteArrayAsync(mediaUrl)));
        }
    }
}
`;

export default Program;