//#region key
var key = "youtube api key";
//#endregion
var monthList = ["Jan", "Feb", "Mar", "Apr", "May", "Jun", "Jul", "Aug", "Sep", "Oct", "Nov", "Dec"];   //used for formatting youtube creation/prublished dates
