let Form2 = `using System;
using System.Windows.Forms;
using System.IO;

namespace InstaGrabber
{
    public partial class Form2 : Form
    {
        private static SaveFileDialog ChooseDirectory = new SaveFileDialog();

        public Form2()
        {
            InitializeComponent();
            this.ControlBox = false; //removes window controls to prevent user from closing the form

        }

        //*********************************************************Accessible Form2 Properties***********************************************************************************************
        public string TextOfDirectoryBox
        {
            get { return DirectoryBox.Text; }
            set { DirectoryBox.Text = value; }
        }

        public string TextOfTxtDirectoryBox
        {
            get { return TxtDirectoryBox.Text; }
            set { TxtDirectoryBox.Text = value; }
        }

        public string TextOfImgurDirectoryBox
        {
            get { return ImgurDirectoryBox.Text; }
            set { ImgurDirectoryBox.Text = value; }
        }

        public string TextOfInstagramDirectoryBox
        {
            get { return InstagramDirectoryBox.Text; }
            set { InstagramDirectoryBox.Text = value; }
        }

        public string TextOfNaverDirectoryBox
        {
            get { return NaverDirectoryBox.Text; }
            set { NaverDirectoryBox.Text = value; }
        }

        public bool SaveToDesktopCheckedProperty
        {
            get { return SaveToDesktopRadioButton.Checked; }
            set { SaveToDesktopRadioButton.Checked = value; }
        }

        public bool SaveToDirectoryCheckedProperty
        {
            get { return SaveToDirectoryRadioButton.Checked; }
            set { SaveToDirectoryRadioButton.Checked = value; }
        }

        public bool ProgrammedDirectoryCheckedProperty
        {
            get { return ProgrammedDirectoryRadioButton.Checked; }
            set { ProgrammedDirectoryRadioButton.Checked = value; }
        }

        //*********************************************************Buttons************************************************************************************************************
        private void SaveToSaveButton_Click(object sender, EventArgs e)
        {
            //makes sure the user is using a directory that exists
            try
            {
                if (SaveToDirectoryRadioButton.Checked)
                {
                    Path.GetFullPath(DirectoryBox.Text);
                }

                if (ProgrammedDirectoryRadioButton.Checked)
                {
                    Path.GetFullPath(TxtDirectoryBox.Text);
                    Path.GetFullPath(ImgurDirectoryBox.Text);
                    Path.GetFullPath(InstagramDirectoryBox.Text);
                    Path.GetFullPath(NaverDirectoryBox.Text);
                }
            }
            catch
            {
                MessageBox.Show("Please Make Sure The Directory Exists!", "Invalid Directory!");
                return;
            }
            this.Hide();
        }

        private void FolderBrowserButton1_Click(object sender, EventArgs e)
        {
            //using a psuedo folder browser with the SaveFileDialog to grab a directory
            ChooseDirectory.FileName = "Select Current Directory With 'Save'.";
            if (ChooseDirectory.ShowDialog() == DialogResult.OK)
                DirectoryBox.Text = Path.GetDirectoryName(ChooseDirectory.FileName);
        }

        private void FolderBrowserButton2_Click(object sender, EventArgs e)
        {
            ChooseDirectory.FileName = "Select Current Directory With 'Save'.";
            if (ChooseDirectory.ShowDialog() == DialogResult.OK)
                TxtDirectoryBox.Text = Path.GetDirectoryName(ChooseDirectory.FileName);
        }

        private void FolderBrowserButton3_Click(object sender, EventArgs e)
        {
            ChooseDirectory.FileName = "Select Current Directory With 'Save'.";
            if (ChooseDirectory.ShowDialog() == DialogResult.OK)
                ImgurDirectoryBox.Text = Path.GetDirectoryName(ChooseDirectory.FileName);
        }

        private void FolderBrowserButton4_Click(object sender, EventArgs e)
        {
            ChooseDirectory.FileName = "Select Current Directory With 'Save'.";
            if (ChooseDirectory.ShowDialog() == DialogResult.OK)
                InstagramDirectoryBox.Text = Path.GetDirectoryName(ChooseDirectory.FileName);
        }

        private void FolderBrowserButton5_Click(object sender, EventArgs e)
        {
            ChooseDirectory.FileName = "Select Current Directory With 'Save'.";
            if (ChooseDirectory.ShowDialog() == DialogResult.OK)
                NaverDirectoryBox.Text = Path.GetDirectoryName(ChooseDirectory.FileName);
        }

        //******************************************************************Radio Buttons*********************************************************************************************
        private void SaveToDesktopRadioButton_CheckedChanged(object sender, EventArgs e)    //enables/disables necessary controls
        {
            if (SaveToDesktopRadioButton.Checked)
            {
                DirectoryBox.Enabled = false;
                FolderBrowserButton1.Enabled = false;
                FolderBrowserButton2.Enabled = false;
                FolderBrowserButton3.Enabled = false;
                FolderBrowserButton4.Enabled = false;
                FolderBrowserButton5.Enabled = false;
                TxtDirectoryBox.Enabled = false;
                ImgurDirectoryBox.Enabled = false;
                InstagramDirectoryBox.Enabled = false;
                NaverDirectoryBox.Enabled = false;
            }
        }

        private void SaveToDirectoryRadioButton_CheckedChanged(object sender, EventArgs e)  //enables/disables necessary controls
        {
            if (SaveToDirectoryRadioButton.Checked)
            {
                DirectoryBox.Enabled = true;
                FolderBrowserButton1.Enabled = true;
                FolderBrowserButton2.Enabled = false;
                FolderBrowserButton3.Enabled = false;
                FolderBrowserButton4.Enabled = false;
                FolderBrowserButton5.Enabled = false;
                TxtDirectoryBox.Enabled = false;
                ImgurDirectoryBox.Enabled = false;
                InstagramDirectoryBox.Enabled = false;
                NaverDirectoryBox.Enabled = false;
            }
        }

        private void ProgrammedDirectoryRadioButton_CheckedChanged(object sender, EventArgs e)   //enables/disables necessary controls
        {
            if (ProgrammedDirectoryRadioButton.Checked)
            {
                DirectoryBox.Enabled = false;
                FolderBrowserButton1.Enabled = false;
                FolderBrowserButton2.Enabled = true;
                FolderBrowserButton3.Enabled = true;
                FolderBrowserButton4.Enabled = true;
                FolderBrowserButton5.Enabled = true;
                TxtDirectoryBox.Enabled = true;
                ImgurDirectoryBox.Enabled = true;
                InstagramDirectoryBox.Enabled = true;
                NaverDirectoryBox.Enabled = true;
                Form1.imgurPreventRd = false;
            }
        }

        
    }
}
`;

export default Form2;