/* eslint-disable */
let Program = `using System;
using System.Collections.Generic;
using System.IO;
using System.Linq;
using System.Net.Http;
using System.Text;
using System.Threading.Tasks;
using Tweetinvi;
using System.Windows.Forms;
using System.Diagnostics;
using System.Threading;
using System.Timers;
using Newtonsoft.Json;

namespace TweetDL
{
    class Program
    {
        #region Credentials
        const string consumerKey = "",
                     consumerSecret = "",
                     userAcessToken = "",
                     userAccessSecret = "";
        #endregion

        #region Variables
        private static int j = 0, folderCount = 0, videoFolderCount = 0, tweetCount = 0, timer = 0, downloadCount = 0;
        private static bool validInput = false, invalidInputCheck, setOptions = false, downloadPhotos = false, downloadVideos = false, sound = true, popup = true, download = false,
                            downloaded = false, duplicate = false, hideUnrelated = false, isReply = false, isQuote = false, isRetweet = false, isOriginalTweet = false, configBreak = false;
        private static HttpClient client = new HttpClient();
        private static StringBuilder sb = new StringBuilder();
        private static string root = Environment.GetFolderPath(Environment.SpecialFolder.Desktop), path, newPath, logPath, es = String.Empty;
        private static Tweetinvi.Models.ITweet newTweet;
        private static List<string> mainProfiles = new List<string>(), bypassVidGifDlProfiles = new List<string>(), hTagExclusion = new List<string>(), excludedScreenNames = new List<string>(),
                                    excludedDls = new List<string>(), excludedPups = new List<string>(), tagsWatchList = new List<string>();
        private static System.Timers.Timer Timer;
        #endregion

        static void Main(string[] args)
        {
            Console.Title = "TweetDL!";
            Init(); //initialize settings/authenticate user
            if (configBreak)
                return;
            
            Console.WriteLine("Loading Users and Filters!");
            var user = User.GetAuthenticatedUser();
            if (user == null) {
                Console.WriteLine("Failed Obtaining Authenticated User!");
                return;
            }
            var stream = Tweetinvi.Stream.CreateFilteredStream();

            #region StreamTags  
            foreach (var item in tagsWatchList) //adds tags to watch for
                stream.AddTrack(item);
            #endregion

            foreach (var following in user.GetFriends()) {
                mainProfiles.Add(following.ScreenName);
                stream.AddFollow(following.Id);
            }

            Console.Clear();
            Console.WriteLine("Users and Filters Loaded!");
            Console.WriteLine("You can edit various inclusions/exclusions in the config.json file found in the \"Twitter\" folder found on the desktop.\nIf the file is broken, delete the file and relaunch the program.");

            Timer = new System.Timers.Timer(1000) { //1 second timer
                Enabled = true
            };

            Timer.Elapsed += new ElapsedEventHandler(OnTimedEvent);

            //attach eventhandler
            stream.MatchingTweetReceived += async (senders, data) => TweetHandler(data); //when a tweet is made, call the handler with the data found
            stream.StartStreamMatchingAnyConditionAsync();

            var t = Console.ReadKey();
            while (t.Key != ConsoleKey.Escape) {
                if (t.Key == ConsoleKey.Enter) { //Enter to restart the stream
                    Console.ForegroundColor = ConsoleColor.Magenta;
                    stream.StopStream();
                    stream.StartStreamMatchingAnyConditionAsync();
                    Console.WriteLine($"{DateTime.Now.ToString("HH:mm:ss")} Stream Restarted!");
                } 
                t = Console.ReadKey();
            }
        }

        private static void OnTimedEvent(Object src, ElapsedEventArgs e)
        {
            timer++;    //1 second timer handler
            Console.Title = $"Now Listening! [Download:{download}] " +
            $"[MediaType:{(!download ? "Downloading None" : (downloadPhotos && downloadVideos ? "All" : (downloadPhotos ? "Images" : "Videos and Gifs")))}] " +
            $"[Sound:{(sound ? "On" : "Off")}] [Popups:{(popup ? "On" : "Off")}] " +
            $"ApproxTimeElapsed: {(timer / 60 / 60).ToString("D2")}:{(timer / 60 % 60).ToString("D2")}:{(timer % 60).ToString("D2")} " +
            $"TweetsThisSession: {tweetCount} NumberOfTweetsDownloaded: {downloadCount}";

        }

        private static async Task TweetHandler(Tweetinvi.Events.MatchedTweetReceivedEventArgs data)
        {
            string profile = String.Empty;
            isReply = false;
            isRetweet = false;
            isQuote = false;
            isOriginalTweet = false;

            try {
                if (hideUnrelated) //hide tweets that are not made by someone you follow
                    if (!mainProfiles.Contains(data.Tweet.CreatedBy.ScreenName))
                        return;

                if (data.Tweet.IsRetweet) { //checks if the tweet is a retweet, quote, reply, or original
                    isRetweet = true;
                    newTweet = data.Tweet.RetweetedTweet;
                }
                else if (data.Tweet.QuotedStatusId != null) {
                    isQuote = true;
                    newTweet = data.Tweet.QuotedTweet;
                }
                else {
                    if (data.Tweet.InReplyToStatusId != null)
                        isReply = true;
                    else
                        isOriginalTweet = true;
                    newTweet = data.Tweet;
                }

                profile = newTweet.CreatedBy.ScreenName;
                tweetCount++;
            }
            catch (Exception e) {
                sb.Append("Failed At: " + data.Tweet.CreatedAt + "  Tweet: " + data.Tweet.Url +
                                    "  [Failed to Load] var tweet = Tweet.GetTweet(" + data.Tweet.Id + ");" + Environment.NewLine + e.Message + Environment.NewLine);
                File.AppendAllText(logPath + "errorLog.txt", sb.ToString());
                sb.Clear();

                if (sound)
                    System.Media.SystemSounds.Exclamation.Play();

                if (popup) {
                    Task.Run(() => {
                    MessageBox.Show("Failed to load tweet!\n" + e.Message +
                    "Check the log!", "ERROR!");
                    });
                }

                var exceptionDetails = ExceptionHandler.GetLastException();
                ExceptionHandler.SwallowWebExceptions = false;
                ExceptionHandler.WebExceptionReceived += (sender, a) => {
                    sb.Append("Failed At: " + data.Tweet.CreatedAt + "  Tweet: " + data.Tweet.Url +
                    "  [Failed to Load] var tweet = Tweet.GetTweet(" + data.Tweet.Id + ");" + a.Value + exceptionDetails + Environment.NewLine);
                    File.AppendAllText(logPath + "errorLog.txt", sb.ToString());
                    sb.Clear();
                };

                return;
            }

            if (excludedScreenNames.Contains(data.Tweet.CreatedBy.ScreenName))  //return if an excluded twitter profile was found
                return;
            if (excludedScreenNames.Contains(profile))
                return;

            foreach (var h in newTweet.Hashtags) {  //check through the tags and return if an excluded tag is found
                if (hTagExclusion.Contains(h.Text))
                    return;
            }

            if (download) {
                downloaded = false;
                
                foreach (var media in newTweet.Entities.Medias) {   //loop through the medias for download
                    if (excludedDls.Contains(newTweet.CreatedBy.ScreenName))    //return if an excluded twitter profile for downloads is found
                        break;

                    if (media.MediaType == "photo") {
                        if (downloadPhotos) {
                            if (Directory.GetFiles(path + @"images" + folderCount + "\\").Length > 1000) {  //if the current images folder has 1000 files, make a new images folder
                                folderCount++;
                                Directory.CreateDirectory(root + @"\Twitter\Downloaded Files\Media\images" + folderCount);
                            }

                            for (int i = 0; i <= folderCount; i++) {    //loop through the images folder to find duplicates, break if found
                                if (File.Exists(path + @"images" + i + "\\" + newTweet.IdStr + "_" + media.IdStr + ".jpg")) {
                                    duplicate = true;
                                    break;
                                }
                            }
                            if (duplicate) { //if duplicate found, continue to the next media
                                duplicate = false;
                                continue;
                            }

                            newPath = path + @"images" + folderCount + "\\" + newTweet.IdStr + "_" + media.IdStr + ".jpg";
                            try {
                                await GetMedia(media.MediaURL, newPath, client);
                                downloaded = true;
                                
                            }
                            catch (Exception e) {
                                sb.Append("Failed At: " + data.Tweet.CreatedAt + "  Tweet: " + data.Tweet.Url +
                                    "  [Failed to Load] var tweet = Tweet.GetTweet(" + data.Tweet.Id + ");" + Environment.NewLine + e.Message + Environment.NewLine);
                                File.AppendAllText(logPath + "errorLog.txt", sb.ToString());
                                sb.Clear();
                                if (sound)
                                    System.Media.SystemSounds.Exclamation.Play();
                                if (popup) {
                                    Task.Run(() => {
                                    MessageBox.Show("\n\nFailed to download:\n" + media.MediaURL + Environment.NewLine + e.Message +
                                    "\nCheck the log!", "ERROR!");
                                    });
                                }
                                return;
                            }
                        }
                        else
                            break;

                    }
                    else if (media.MediaType == "video") {
                        int comparitorVal = 0;
                        string url = string.Empty;

                        if (downloadVideos || bypassVidGifDlProfiles.Contains(newTweet.CreatedBy.ScreenName)) { //check for twitter profiles that would bypass if downloadVideos is false
                            if (Directory.GetFiles(path + @"videos" + videoFolderCount + "\\").Length > 400) {  //if the current videos folder has 1000 files, make a new videos folder
                                videoFolderCount++;
                                Directory.CreateDirectory(root + @"\Twitter\Downloaded Files\Media\videos" + videoFolderCount);
                            }
                            for (int i = 0; i <= videoFolderCount; i++) { //if the video has already been downloaded return because tweets only hold one video if one is included
                                if (File.Exists(path + @"videos" + videoFolderCount + "\\" + newTweet.IdStr + "_" + media.IdStr + ".mp4"))
                                    return;
                            }

                            newPath = path + @"videos" + videoFolderCount + "\\" + newTweet.IdStr + "_" + media.IdStr + ".mp4";
                            j = 0;

                            foreach (var x in media.VideoDetails.Variants) {    //loop through the video data and find the link which contains mp4
                                if (x.URL.Contains("mp4")) {
                                    if (comparitorVal < Convert.ToInt32(x.URL.Split('/', '/')[7].Replace("x", ""))) {   //grab a specific part of the link indicating resolution, and grab the largest/highest quality
                                        url = x.URL;
                                        comparitorVal = Convert.ToInt32(x.URL.Split('/', '/')[7].Replace("x", ""));
                                    }
                                }
                            }
                            try {
                                await GetMedia(url, newPath, client);
                            }
                            catch (Exception e) {
                                sb.Append("Failed At: " + data.Tweet.CreatedAt + "  Tweet: " + data.Tweet.Url +
                                "  [Failed to Load] var tweet = Tweet.GetTweet(" + data.Tweet.Id + ");" + Environment.NewLine + e.Message + Environment.NewLine);
                                File.AppendAllText(logPath + "errorLog.txt", sb.ToString());
                                sb.Clear();
                                if (sound)
                                    System.Media.SystemSounds.Exclamation.Play();
                                if (popup) {
                                    Task.Run(() => {
                                        MessageBox.Show("\n\nFailed to download:\n" + media.MediaURL + Environment.NewLine + e.Message +
                                        "\nCheck the log!", "ERROR!");
                                    });
                                }
                                return;
                            }
                            downloaded = true;
                        }
                        else
                            break;
                    }
                    else if (media.MediaType == "animated_gif") {
                        if (downloadVideos || bypassVidGifDlProfiles.Contains(newTweet.CreatedBy.ScreenName)) { //check for twitter profiles that would bypass if downloadVideos is false
                            if (Directory.GetFiles(path + @"videos" + videoFolderCount + "\\").Length > 400) {  //if the current videos folder has 1000 files, make a new videos folder
                                videoFolderCount++;
                                Directory.CreateDirectory(root + @"\Twitter\Downloaded Files\Media\videos" + videoFolderCount);
                            }

                            for (int i = 0; i <= videoFolderCount; i++) { //if the video has already been downloaded return because tweets only hold one video if one is included
                                if (File.Exists(path + @"videos" + videoFolderCount + "\\" + newTweet.IdStr + "_" + media.IdStr + ".mp4"))
                                    return;
                            }

                            newPath = path + @"videos" + videoFolderCount + "\\" + newTweet.IdStr + "_" + media.IdStr + ".mp4";
                            try {
                                await GetMedia(media.VideoDetails.Variants[0].URL, newPath, client);
                            }
                            catch (Exception e) {
                                sb.Append("Failed At: " + data.Tweet.CreatedAt + "  Tweet: " + data.Tweet.Url +
                                        "  [Failed to Load] var tweet = Tweet.GetTweet(" + data.Tweet.Id + ");" + Environment.NewLine + e.Message + Environment.NewLine);
                                File.AppendAllText(logPath + "errorLog.txt", sb.ToString());
                                sb.Clear();
                                if (sound)
                                    System.Media.SystemSounds.Exclamation.Play();
                                if (popup) {
                                    Task.Run(() =>
                                    {
                                    MessageBox.Show("\n\nFailed to download:\n" + media.MediaURL + Environment.NewLine + e.Message +
                                    "\nCheck the log!", "ERROR!");

                                    });
                                }
                                return;
                            }
                        }
                        else
                            break;

                        downloaded = true;
                    }
                }
                
            }
            if (downloaded) {   //change console write color if a tweeted media was downloaded
                downloadCount++;
                Console.ForegroundColor = ConsoleColor.DarkGreen;
            }
            else
                Console.ForegroundColor = ConsoleColor.DarkYellow;

            if (mainProfiles.Contains(profile)) //if the tweet was made to/about someone followed
                Console.ForegroundColor = ConsoleColor.DarkCyan;

            if (mainProfiles.Contains(data.Tweet.CreatedBy.ScreenName)) {   //if the tweet was made by someone followed
                Console.ForegroundColor = ConsoleColor.DarkMagenta;
                if (sound)
                    System.Media.SystemSounds.Asterisk.Play();

                string temp = data.Tweet.Url;   //save url to avoid the url changing when a new tweet is found

                if (popup && !excludedPups.Contains(newTweet.CreatedBy.ScreenName) && !isReply) {   //setup the popup, does not display replies
                    Task.Run(() => {
                        var dialogResult = MessageBox.Show($"A new tweet by: {data.Tweet.CreatedBy.ScreenName}!" +
                            $"{(data.Tweet.IsRetweet ? "\nThey Retweeted: " + newTweet.CreatedBy.ScreenName + "!" : ((data.Tweet.QuotedStatusId != null) ? "\nThey Quoted: " + newTweet.CreatedBy.ScreenName + "!" : es))}" +
                            $"\nOn: {data.Tweet.CreatedAt.ToShortDateString()} At: {data.Tweet.CreatedAt.ToShortTimeString()}\n\n" +
                            $"Tweet:\n( {data.Tweet.FullText} )\n\nOpen the Tweet in a browser?", "New Tweet!", MessageBoxButtons.OKCancel);

                        if (dialogResult == DialogResult.OK)
                            Process.Start(temp);
                    });
                }
            }
            //update status on title bar of console
            Console.Title = $"Now Listening! [Download:{download}] " +
            $"[MediaType:{(!download ? "Downloading None" : (downloadPhotos && downloadVideos ? "All" : (downloadPhotos ? "Images" : "Videos and Gifs")))}] " +
            $"[Sound:{(sound ? "On" : "Off")}] [Popups:{(popup ? "On" : "Off")}] " +
            $"ApproxTimeElapsed: {(timer / 60 / 60).ToString("D2")}:{(timer / 60 % 60).ToString("D2")}:{(timer % 60).ToString("D2")} " +
            $"TweetsThisSession: {tweetCount} NumberOfTweetsDownloaded: {downloadCount}";
            //writes to the console of the actions made
            Console.WriteLine($"{data.Tweet.CreatedAt.TimeOfDay} [Download={downloaded}]{(downloaded ? " " : es)} " +
                $"[HasMedia={(newTweet.Entities.Medias.Count > 0)}{((newTweet.Entities.Medias.Count > 0) ? ", " + newTweet.Entities.Medias[0].MediaType : es)}]" +
                $"{((!(newTweet.Entities.Medias.Count > 0))?"\t":es)}\t[Orig:{data.Tweet.CreatedBy.ScreenName}]{((data.Tweet.CreatedBy.ScreenName.Length < 9) ? "\t" : es)}\t[{(data.Tweet.IsRetweet ? "Retweeted: " + newTweet.CreatedBy.ScreenName : ((data.Tweet.QuotedStatusId != null) ? "They Quoted: " + newTweet.CreatedBy.ScreenName : (isReply ?  "Replying to: " + data.Tweet.InReplyToScreenName: "Original Tweet")))}]" +
                $"{(isRetweet ? ((newTweet.CreatedBy.ScreenName.Length < 11) ? "\t" : es) : es)}{(isQuote ? ((newTweet.CreatedBy.ScreenName.Length < 9) ? "\t" : es) : es)}{(isReply ? ((data.Tweet.InReplyToScreenName.Length < 9) ? "\t" : es) : es)}{(isOriginalTweet ? "\t": es)}\t( {data.Tweet.Url} )");

        }

        private static void Init()
        {
            string[] options = { "help", "dl", "all", "img", "vid", "soff", "puoff", "mm", "twice", "vtubers", "watchall", "hu" };
            List<string> input = new List<string>();
            string newInput = string.Empty, tempJson = string.Empty;

            Console.SetBufferSize(250, 32000);  //setup console size and color
            Console.ForegroundColor = ConsoleColor.DarkYellow;

            Auth.SetUserCredentials(consumerKey, consumerSecret, userAcessToken, userAccessSecret); //authenticate user
            RateLimit.RateLimitTrackerMode = RateLimitTrackerMode.TrackAndAwait;

            Directory.CreateDirectory(root + @"\Twitter\Downloaded Files\Media\");  //creates directory if it isn't already made
            Directory.CreateDirectory(root + @"\Twitter\Downloaded Files\Media\images0\");
            Directory.CreateDirectory(root + @"\Twitter\Downloaded Files\Media\videos0\");

            if (!File.Exists(root + @"\Twitter\errorLog.txt"))  //create the errorLog file if it isn't already made
                File.Create(root + @"\Twitter\errorLog.txt").Close();

            path = root + @"\Twitter\Downloaded Files\Media\";
            logPath = root + @"\Twitter\";

            while (Directory.Exists(root + @"\Twitter\Downloaded Files\Media\images" + folderCount))    //grab the number for the current media folders to be downloaded to
                folderCount++;
            folderCount--;
            while (Directory.Exists(root + @"\Twitter\Downloaded Files\Media\videos" + videoFolderCount))
                videoFolderCount++;
            videoFolderCount--;

            while (!setOptions) {   //sets settings of the program depending on what was input
                while (!validInput) { //loop until an accepted input is used
                    List<string> invalidInputs = new List<string>();
                    Console.WriteLine("version 0.56");
                    Console.WriteLine("What do you want to do?(help, dl + [all, img, vid], soff, puoff, mm, hu(hide unrelated tweets))\nFor more info on the commands, type help.");
                    input = Console.ReadLine().Split(' ').ToList(); //splits input and stores into input variable to be checked
                    invalidInputCheck = false;
                    newInput = string.Join(" ", input);
                    foreach (string ip in input) {
                        if (!(options.Any(ip.Equals))) { //if any of the inputted strings do not match with accepted inputs add to invalidInputs List
                            invalidInputs.Add(ip);
                            invalidInputCheck = true;
                        }
                    }
                    if (invalidInputCheck)
                        Console.WriteLine($"\nThe following are invalid commands: {string.Join(", ", invalidInputs)}\n");
                    else if (ContainsAll(newInput, new string[] { "all", "img" }))  //if a wrong input combination is used
                        Console.WriteLine("\nPair 'dl' with only one of the media settings(dl all, dll img, or dl vid).\n");
                    else if (ContainsAll(newInput, new string[] { "all", "vid" }))  //if a wrong input combination is used
                        Console.WriteLine("\nPair 'dl' with only one of the media settings(dl all, dll img, or dl vid).\n");
                    else if (ContainsAll(newInput, new string[] { "img", "vid" }))  //if a wrong input combination is used
                        Console.WriteLine("\nPair 'dl' with only one of the media settings(dl all, dll img, or dl vid).\n");
                    else
                        validInput = true;
                }

                if (newInput.Contains("help")) {    //if help was inputted
                    Console.WriteLine("\nThe Valid Commands Are:\n" +
                                      "help \t= Displays the list of valid commands\n" +
                                      "dl \t= Allows the download of media.\n" +
                                      "all \t= Downloads both images and videos/gifs.\n" +
                                      "img \t= Downloads only images.\n" +
                                      "vid \t= Downloads only videos/gifs.\n" +
                                      "soff \t= Turns off notification sounds.\n" +
                                      "puoff \t= Turns off notification popups.\n" +
                                      "mm \t= Watches for tweets but does not download(Monitor Mode).\n\n");

                    validInput = false;
                }
                else if(newInput.Contains("mm")) {
                    setOptions = true;
                    //does not download tweets
                }
                else {
                    setOptions = true;
                    if (newInput.Contains("dl")) {  //download media
                        download = true;
                        if (newInput.Contains("all")) { //download all
                            downloadPhotos = true;
                            downloadVideos = true;
                        }
                        else if (newInput.Contains("img"))  //only download images 
                            downloadPhotos = true;
                        else if (newInput.Contains("vid")) //only download videos 
                            downloadVideos = true;
                        else {  //if no type was chosen
                            Console.WriteLine("\nA media type was not chosen.\n");
                            validInput = false;
                            setOptions = false;
                        }
                    }
                    else if (newInput.Contains("all") || newInput.Contains("img") || newInput.Contains("vid")) {  //if download was not input
                        Console.WriteLine("\nUse the command 'dl' if you want to download media.\n");
                        validInput = false;
                        setOptions = false;
                    }
                }

                if (newInput.Contains("soff"))  //sound off
                    sound = false;
                if (newInput.Contains("puoff")) //popups off
                    popup = false;
                if (newInput.Contains("hu"))    //hide unrelated twitter profiles from being processed
                    hideUnrelated = true;

                if (!File.Exists(root + @"\Twitter\config.json")) { //if config file was not created
                    File.Create(root + @"\Twitter\config.json").Close();

                    sb.Append("{\"tagsWatchList\": \"\",\"bypassVidGifDlProfiles\": \"\",\"hTagExclusion\": \"\",\"excludedScreenNames\": \"\",\"excludedDls\": \"\",\"excludedPups\": \"\"}");
                    File.AppendAllText(logPath + "config.json", sb.ToString());
                    sb.Clear();
                    Console.WriteLine("You can edit various inclusions/exclusions in the config.json file found in the \"Twitter\" folder found on the desktop.\nIf the file is broken, delete the file and relaunch the program.");
                    Console.WriteLine("Separate using using a single comma with no spaces. ie: \"cats,dogs,ice cream,tag21\" etc.");
                    Console.WriteLine("tagsWatchList: list of tags to watch for\nbypassVidGifDlProfiles: list of twitter profiles which will download video media even with the option turned off\n" +
                        "hTagExclusion: list of tags to exlude\nexcluddedScreenNames: list of twitter profles to exclude\nexcludedDls: list of twitter profiles to not download media from\n" +
                        "excludedPups: list of twitter profiles that will not trigger a popup\n");
                }
                else {
                    try {   //load strings from config.json
                        tempJson = File.ReadAllText(root + @"\Twitter\config.json");
                        var settings = JsonConvert.DeserializeObject<ConfigMap>(tempJson);
                        tagsWatchList = settings.tagsWatchList.Split(',').ToList();
                        bypassVidGifDlProfiles = settings.bypassVidGifDlProfiles.Split(',').ToList();
                        hTagExclusion = settings.hTagExclusion.Split(',').ToList();
                        excludedScreenNames = settings.excludedScreenNames.Split(',').ToList();
                        excludedDls = settings.excludedDls.Split(',').ToList();
                        excludedPups = settings.excludedPups.Split(',').ToList();
                    }
                    catch (Exception) {
                        Console.WriteLine("Something went wrong with the config.json file, fix the file or delete it and rerun the program to get a fresh file.");
                        configBreak = true;
                        return;
                    }
                }
            }
            Console.WriteLine($"\nSetting have been changed...\n[Download:{download}] " +
            $"[MediaType:{(!download ? "Downloading None" : (downloadPhotos && downloadVideos ? "All" : (downloadPhotos ? "Images" : "Videos and Gifs")))}] " +
            $"[Sound:{(sound ? "On" : "Off")}] [Popups:{(popup ? "On" : "Off")}]");

            Console.Title = $"[Download:{download}] " +
            $"[MediaType:{(!download ? "Downloading None" : (downloadPhotos && downloadVideos ? "All" : (downloadPhotos ? "Images" : "Videos and Gifs")))}] " +
            $"[Sound:{(sound ? "On" : "Off")}] [Popups:{(popup ? "On" : "Off")}] " +
            $"ApproxTimeElapsed: {(timer / 60 / 60).ToString("D2")}:{(timer / 60 % 60).ToString("D2")}:{(timer % 60).ToString("D2")} " +
            $"TweetsThisSession: {tweetCount} NumberOfTweetsDownloaded: {downloadCount}";
        }

        public static bool ContainsAll(string source, string[] values)
        {
            return values.All(x => source.Contains(x));
        }

        private static async Task GetMedia(string mediaUrl, string downloadPath, HttpClient client) //function used to download media
        {
            File.WriteAllBytes(downloadPath, (await client.GetByteArrayAsync(mediaUrl)));
        }
    }

    class ConfigMap //mapping of config.json file
    {
        [JsonProperty("tagsWatchList")]
        public string tagsWatchList { get; set; }

        [JsonProperty("bypassVidGifDlProfiles")]
        public string bypassVidGifDlProfiles { get; set; }

        [JsonProperty("hTagExclusion")]
        public string hTagExclusion { get; set; }

        [JsonProperty("excludedScreenNames")]
        public string excludedScreenNames { get; set; }

        [JsonProperty("excludedDls")]
        public string excludedDls { get; set; }

        [JsonProperty("excludedPups")]
        public string excludedPups { get; set; }
    }

}
`;

export default Program;