let InstagramPostMap = `using Newtonsoft.Json;

namespace ConsoleApp3
{
    public class InstagramPost
    {
        [JsonProperty("entry_data")]
        public EntryData entryData { get; set; }

        public class EntryData
        {
            [JsonProperty("PostPage")]
            public PostPage[] postPage { get; set; }
        }

        public class PostPage
        {
            [JsonProperty("graphql")]
            public Graphql graphql { get; set; }
        }

        public class Graphql
        {
            [JsonProperty("shortcode_media")]
            public Media media { get; set; }
        }

        public class Media
        {
            public string __typename { get; set; }
            public string shortcode { get; set; }
            public string video_url { get; set; }
            public string taken_at_timestamp { get; set; }

            [JsonProperty("edge_media_to_caption")]
            public Caption caption { get; set; }

            [JsonProperty("display_resources")]
            public Resources[] resources { get; set; }

            [JsonProperty("owner")]
            public Owner owner { get; set; }

            [JsonProperty("edge_sidecar_to_children")]
            public Children children { get; set; }
        }

        public class Caption
        {
            [JsonProperty("edges")]
            public CaptionEdges[] captionEdges { get; set; }
        }

        public class CaptionEdges
        {
            [JsonProperty("node")]
            public CaptionNode captionNode { get; set; }
        }

        public class CaptionNode
        {
            public string text { get; set; }
        }

        public class Children
        {
            [JsonProperty("edges")]
            public Edges[] edges { get; set; }
        }

        public class Edges
        {
            [JsonProperty("node")]
            public Node node { get; set; }
        }

        public class Node
        {
            public string video_url { get; set; }
            public string __typename { get; set; }

            [JsonProperty("display_resources")]
            public Resources[] resources { get; set; }
        }

        public class Owner
        {
            public string username { get; set; }
        }

        public class Resources
        {
            public string src { get; set; }
        }

    }
}
`;

export default InstagramPostMap;