import React, { useState, useRef } from "../../node_modules/react/index";

const BottomContainer = (props) => {
    const sRef = useRef(null);
    const [zoom, setZoom] = useState("");
    
    const imgZoom = e => {  
        document.body.style.overflow = "hidden";
        sRef.current.style.visibility = "visible";
        setZoom(<img className="zoomedImage" alt="" src={e} onClick={e => {document.body.style.overflow = "auto"; setZoom(""); sRef.current.style.visibility = "hidden";}}></img>)
    };

    return(
        <div>
            <div className="bottomContainer">
                    <div className="pictureContainer">
                        <img className="picture" alt="" src={`resources/s/${props.selectedS}`} onClick={e => imgZoom(`resources/s/${props.selectedS}`)}></img>
                    </div>
                    <div className="sPanel">
                        {props.loop2}
                    </div>
                    <div className="zoomedImageContainer" ref={sRef}>
                        {zoom}
                    </div>
                </div>
        </div>
    );
}

export default BottomContainer;