let scraper2Map = `using Newtonsoft.Json;

namespace ImageGrabberConsoleApp.Maps
{
    public class Scraper2Map
    {
        [JsonProperty("itemListElement")]
        public ItemListElement[] ItemListElements { get; set; }

        public class ItemListElement
        {
            public string id { get; set; }
            public string file_url { get; set; }
            public string jpeg_url { get; set; }
        }
    }
}
`;

export default scraper2Map;