let VliveMap = `using Newtonsoft.Json;

namespace InstaGrabber
{
    class VliveMap
    {
        [JsonProperty("result")]
        public Result result { get; set; }

        public class Result
        {
            public string totalVideoCount { get; set; }

            [JsonProperty("channelInfo")]
            public ChannelInfo channelInfo { get; set; }

            [JsonProperty("videoList")]
            public VideoList[] videoList { get; set; }
        }

        public class ChannelInfo
        {
            public string channelSeq { get; set; }
            public string channelCode { get; set; }
            public string channelName { get; set; }
            public string fanCount { get; set; }
            public string upcomingShowYn { get; set; }
            public string channelProfileImage { get; set; }
            public string channelCoverImage { get; set; }
            public string comment { get; set; }
        }

        public class VideoList
        {
            public string videoSeq { get; set; }
            public string videoType { get; set; }
            public string title { get; set; }
            public string playCount { get; set; }
            public string likeCount { get; set; }
            public string commentCount { get; set; }
            public string thumbnail { get; set; }
            public string willStartAt { get; set; }
            public string onAirStartAt { get; set; }
            public string @type { get; set; }
        }
    }
}
`;

export default VliveMap;