
let AutoInstaGrab = `using System;
using System.Windows.Forms;
using System.Net.Http;
using System.Linq;
using ConsoleApp3;
using Newtonsoft.Json;
using static ConsoleApp3.InstagramProfile;
using Newtonsoft.Json.Linq;
using System.Threading.Tasks;

namespace InstaGrabber
{
    public class AutoInstaGrab
    {
        //***********************************************************************GLOBAL VARIABLES*************************************************************************************************
        #region Variables
        private static string[] Post = new string[12];  //post[] holds the 12 posts from the profile

        public int toDownloadCount;             //keeps tracck of how many posts need to be downloaded

        public string[] postsToDownload, commentsDownloaded;        //holds the posts which need to be downloaded

        public string profileAt;               //name of the profile
        public string shortCode;
        private static string Url;                      //url of the profile

        private bool[] toDownload = new bool[12];//keeps track of which index needs to be downloaded, parallel with postsToDownload

        public bool failedDownload;             //if failed to grab html at one of the two points
        private static readonly DateTime epoch = new DateTime(1970, 1, 1, 0, 0, 0, DateTimeKind.Utc);
        public DateTime[] postUploadDateAndTime = new DateTime[12];
        public DateTime latestPost;
        public string[] postShortCodes = new string[12];

        #endregion
        //***************************************************************************************************************************************************************************************

        //**********************************************************************METHODS**********************************************************************************************************
        public void AutoGrab(string url, DateTime lastPostDateAndTime, HttpClient client, HtmlAgilityPack.HtmlDocument doc)
        {
            failedDownload = false;
            Url = url;

            try
            {
                string html = client.GetStringAsync(url).GetAwaiter().GetResult();
                GrabPosts(html, lastPostDateAndTime, client, doc);
            }
            catch (Exception e)
            {

                Task.Run(() =>
                {
                    System.Media.SystemSounds.Exclamation.Play();
                    MessageBox.Show(Url + "\nFailed to download.\nProfile uses a different format.\n" + e, "Url Failed!");
                });

                failedDownload = true;
                return;
            }
        }

        public void GrabPosts(string html, DateTime lastPostDateAndTime, HttpClient client, HtmlAgilityPack.HtmlDocument doc)
        {
            string[] postUrls = new string[12], postCaptions = new string[12], shortCodes = new string[12];
            string json;
            toDownloadCount = 0;
            //doc = new HtmlAgilityPack.HtmlDocument();
            doc.LoadHtml(html);
            json = doc.DocumentNode.Descendants().Where(x => x.InnerText.StartsWith("window._sharedData")).First().InnerText;
            var data = JsonConvert.DeserializeObject<InstagramProfile>(json.Substring(21, json.Length - 22));
            var profile = data.entryData.profilePage[0].graphql.user.username;
            profileAt = "@" + profile;
            
            latestPost = epoch.AddSeconds(Convert.ToDouble(data.entryData.profilePage[0].graphql.user.postEdges.edges[0].node.taken_at_timestamp));
            if (lastPostDateAndTime == latestPost)
                return;
            var profileEdge = data.entryData.profilePage[0].graphql.user.postEdges.edges;
            
            int k = 0;
            foreach (Edges e in profileEdge)
            {
                var eToUtc = epoch.AddSeconds(Convert.ToDouble(e.node.taken_at_timestamp));
                if (eToUtc > lastPostDateAndTime)
                    toDownloadCount++;
                else
                    break;

                string caption = string.Empty;
                postUrls[k] = "https://www.instagram.com/p/" + e.node.shortcode;

                if (e.node.captionEdge.edges2.Any())
                    caption = e.node.captionEdge.edges2[0].node2.text;
                else
                    caption = "No Caption!";

                shortCodes[k] = shortCode;
                postCaptions[k] = caption;
                postUploadDateAndTime[k] = eToUtc;

                k++;
            }

            commentsDownloaded = new string[toDownloadCount];
            postsToDownload = new string[toDownloadCount];  //saves each new post which has not been downloaded yet into a new array
            int w = 0;
            for (int y = toDownloadCount - 1; y >= 0; y--)
            {
                commentsDownloaded[w] = postCaptions[y];
                postShortCodes[w] = shortCodes[y];
                postsToDownload[w] = postUrls[y];
                w++;
            }
        }
        //***************************************************************************************************************************************************************************************

    }
}
`;

export default AutoInstaGrab;