let groupLiveInfoRoute = `const express = require('express');
const router = express.Router();
const Group = require('../models/groupLiveData');

router.get('/', (req, res, next) => {   //return data of all channels' live status
    Group.find()
        .select('-_id -__v -members._id')
        .exec()
        .then(doc => {
            res.status(200).json(doc);
        })
        .catch(error => {
            console.log(error);
            res.status(500).json({error: error});
        });
});

router.get('/group1', (req, res, next) => {   //return data of live status of channels in group1
    Group.findById('group1_Live')
        .select('-_id -__v -members._id')
        .exec()
        .then(doc => {
            res.status(200).json(doc);
        })
        .catch(error => {
            console.log(error);
            res.status(500).json({error: error});
        });
});

router.get('/group2', (req, res, next) => {   //return data of live status of channels in group2
    Group.findById('group2_Live')
        .select('-_id -__v -members._id')
        .exec()
        .then(doc => {
            res.status(200).json(doc);
        })
        .catch(error => {
            console.log(error);
            res.status(500).json({error: error});
        });
});

router.get('/group3', (req, res, next) => {   //return data of live status of channels in group3
    Group.findById('group3_Live')
        .select('-_id -__v -members._id')
        .exec()
        .then(doc => {
            res.status(200).json(doc);
        })
        .catch(error => {
            console.log(error);
            res.status(500).json({error: error});
        });
});

module.exports = router;`;

export default groupLiveInfoRoute;