import React, { useEffect, useState, useRef } from "../../node_modules/react/index";
import Prism from '../../node_modules/prismjs/prism.js';
import {data as metaData} from "../metaData.js";
import "../prism-working.css"
import BottomContainer from "./BottomContainer";

const Body = () => {
    //-----------------SideNav
    let currentProject = "";
    const [selectedProject, setSelectedProject] = useState("test");
    const [selectedSourceCode, setSelectedSourceCode] = useState("test2");
    const [mainLoop, setMainLoop] = useState("");

    const setPage = index => {
        if (currentProject === index) 
            return; 
        else 
            currentProject = index;
        const scButtons = [];
        const thumbnails = [];
        
        current = metaData.project[index].sourceCode[0].fileName;

        if (metaData.project[index].sourceCode.length > 0) {
            metaData.project[index].sourceCode.forEach(e => {
                scButtons.push(<button key={e.id} className="button2" onClick={b => buttonHandler(e.path, e.language, e.fileName)}>{e.fileName}</button>);
            });
        }
            
        if (metaData.project[index].screenShots.length > 0) {
            metaData.project[index].screenShots.forEach(e => {
                thumbnails.push(<div key={e.id} className="sThumbContainer"><img className="sThumb" alt="" src={`resources/s/${e.path}`} onClick={b => setMainPicture(e.path)}></img></div>);
            });
            setMainPicture(metaData.project[index].screenShots[0].path);
        }
        else
            setMainPicture("noPictures.png");

        setDescription(metaData.project[index].description);
        setSelectedProject(metaData.project[index].name);
        setSelectedSourceCode(metaData.project[index].sourceCode[0].fileName);
        
        setLoop1(scButtons);
        setLoop2(thumbnails);

        
        data(metaData.project[index].sourceCode[0].path).then(res => {
            setSC(res);
            scRef.current.scrollTop = 0;
            Prism.highlightAll();
        });
        
        //setSC(metaData.project[index].sourceCode[0].path);
        //scRef.current.scrollTop = 0;

        
    };
    //-----------------SideNav end

    //-----------------TopContainer
    let current = "";
    const scRef = useRef(null);
    const [loop1, setLoop1] = useState("");
    const [description, setDescription] = useState(metaData.project[0].description);
    const [sc, setSC] = useState("");
    const [language, setLanguage] = useState(metaData.project[0].sourceCode[0].language);

    //using async data() with .then does the Prism.highlightAll() faster than a callback with useEffect
    const data = async (path) => {
        /* //source code files located in public and not src
        const res = await fetch(`resources/sc/${path}`, {
            method: "get"
        });
        return res.text();
        */
        return path;
    }
    

    const buttonHandler = (e, l, n) => {
        if (current === n)
            return;
        else
            current = n;
        setLanguage(l);
        setSelectedSourceCode(n);
        
        data(e).then(res => {
            setSC(res);
            scRef.current.scrollTop = 0;
            Prism.highlightAll();
        });
        
        //setSC(e);
        //scRef.current.scrollTop = 0;

        
    };
    //-----------------TopContainer end

    //-----------------BottomContainer
    const [loop2, setLoop2] = useState("");
    const [selectedS, setSelectedS] = useState(metaData.project[0].screenShots[0]);
    let currentPicture = "";

    const setMainPicture = e => {
        if (currentPicture === e) 
            return; 
        else 
            currentPicture = e;
        setSelectedS(e);
    };
    //-----------------BottomContainer end

    /* if not using async function with callback(.then)
    useEffect(() => {
        Prism.highlightAll();
    }, [sc]);
    */

    useEffect( () => {
        const sideNavButtons = [];
        metaData.project.forEach(e => {
            sideNavButtons.push(<button key={e.index} className={`button1`} onClick={b => {setPage(e.index); }}>{e.name}</button>);
        });
        setMainLoop(sideNavButtons);
        setPage(0);
        
        // eslint-disable-next-line
    }, []);
    
    return (
        <div>
            <div className="sideNav">
                <div className="selected">Selected:<br />{selectedProject}, {selectedSourceCode}</div>
                {mainLoop}
            </div>

            <div className="mainContainer">
                <div className="description">Description: {description}</div>
                <div className="topContainer">
                    <div className="scPanel">
                        {loop1}
                    </div>
                    <div className="scContainer">
                        <pre className="line-numbers" ref={scRef}><code className={`language-${language}`}>{sc}</code></pre>
                    </div>
                </div>
                <BottomContainer selectedS={selectedS} loop2={loop2}></BottomContainer>
            </div>
        </div>
    );
}

export default Body;