/* eslint-disable */
let Program = `using System;
using System.Net.Http;
using ImageGrabberConsoleApp.Grabbers;

namespace ImageGrabberConsoleApp
{
    class Program
    {
        private static readonly HttpClient client = new HttpClient();
        static void Main(string[] args)
        {
            Scraper1 scraper1 = new Scraper1();
            Scraper2 scraper2 = new Scraper2();
            Scraper3 scraper3 = new Scraper3();

            Console.Title = $"StandBy, waiting for url.";
            while (true)
            {
                Console.WriteLine("Enter the Url and number(blank for max pages) of the pages to be processed. \"exit\" to quit.");
                var url = Console.ReadLine().Split(' ');    //two inputs, url and desired number of pages to download
                var numOfPages = 0;
                if (url.Length > 1)
                {
                    if (int.TryParse(url[1], out _))
                        numOfPages = int.Parse(url[1]);
                    else
                    {
                        Console.WriteLine("The second input was not a number!");
                        continue;
                    }    
                }
                if (url[0].Equals("exit", StringComparison.OrdinalIgnoreCase))
                    break;
                if (url[0].Length <= 5)
                    continue;
                if (url[0].Contains("scraper1 url", StringComparison.OrdinalIgnoreCase))
                    scraper1.Scrape(url[0], client, numOfPages);
                else if (url[0].Contains("scraper2 url", StringComparison.OrdinalIgnoreCase) || url[0].Contains("scraper2 url 2", StringComparison.OrdinalIgnoreCase))
                    scraper2.Scrape(url[0], client, numOfPages);
                else if (url[0].Contains("scraper3 url", StringComparison.OrdinalIgnoreCase))
                    scraper3.Scrape(url[0], client, numOfPages);
                else
                    Console.WriteLine("The url is not supported by this program!");
                
            }
            Console.WriteLine("Program ready to exit.");
        }
    }
}
`;

export default Program;