let InstagramNextPageMap = `using Newtonsoft.Json;
using System;
using System.Collections.Generic;
using System.Linq;
using System.Text;
using System.Threading.Tasks;

namespace instagramPagesTest
{
    class InstagramNextPage
    {
        [JsonProperty("data")]
        public Data data { get; set; }

        public class Data
        {
            [JsonProperty("user")]
            public User user { get; set; }
        }

        public class User
        {
            [JsonProperty("edge_owner_to_timeline_media")]
            public EdgeToMedia edgeToMedia { get; set; }
        }

        public class EdgeToMedia
        {
            [JsonProperty("page_info")]
            public PageInfo pageInfo { get; set; }

            [JsonProperty("edges")]
            public Edges[] edges { get; set; }
        }

        public class PageInfo
        {
            public string end_cursor { get; set; }
        }

        public class Edges
        {
            [JsonProperty("node")]
            public MediaNode mediaNode { get; set; }
        }

        public class MediaNode
        {
            public string id { get; set; }
            public string __typename { get; set; }
            public string shortcode { get; set; }
            public string taken_at_timestamp { get; set; }

            [JsonProperty("edge_media_to_caption")]
            public EdgeMediaToCaption edgeMediaToCaption { get; set; }
        }

        public class EdgeMediaToCaption
        {
            [JsonProperty("edges")]
            public CaptionEdge[] captionEdge { get; set; }
        }

        public class CaptionEdge
        {
            [JsonProperty("node")]
            public CaptionNode captionNode { get; set; }
        }

        public class CaptionNode
        {
            public string text { get; set; }
        }
    }
}
`;

export default InstagramNextPageMap;