            if (AutoUpdateCheckBox.Checked)
            {
                //AutoUpdateTimer.Enabled = true;
                CurrentTime.Enabled = true;
            }
            else
            {
                //AutoUpdateTimer.Enabled = false;
                CurrentTime.Enabled = false;
            }
            if (String.IsNullOrEmpty(AutoCheckIntervalBox.Text))
                AutoCheckIntervalBox.Text = "1800";
            countDown = Convert.ToInt32(AutoCheckIntervalBox.Text);
            countDownPrevValue = countDown;
            //AutoUpdateTimer.Interval = countDownPrevValue * 1000;

            ServicePointManager.Expect100Continue = true;
            ServicePointManager.SecurityProtocol = SecurityProtocolType.Tls
                   | SecurityProtocolType.Tls11
                   | SecurityProtocolType.Tls12
